var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("沙赞")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        沙赞一本漫画书 的超级英雄出现在出版物由美国出版商 DC漫画。艺术家CC Beck和作家Bill Parker于1939年创作了这个角色.Marvel上尉首次出现在由Fawcett Comics出版的Whiz Comics＃2（1940 年 2月的封面）中。他是Billy Batson的另一个自我，他是一个男孩，通过说出“ SHAZAM！ ”（六个“不朽长老”的首字母缩写词：S olomon， H ercules， A tlas， Z eus， A chilles和 M emcury）可以将自己变成一个具有超人力量，速度，飞行和其他能力的服装成年人。角色与广泛的盗贼画廊进行了战斗，主要是 Sivana博士， Mind先生和 Black Adam的敌人。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        根据图书销售情况，这个角色是20世纪40年代最受欢迎的超级英雄，甚至超过了超人。Fawcett扩大了特许经营范围，包括其他“奇迹”，主要是Marvel Family的同事Mary Marvel和Marvel Jr.，他们也可以利用比利的力量。Marvel上尉也是第一部被改编成电影的漫画超级英雄，在1941年的共和国图片 系列中，Marvel上尉冒险，Tom Tyler饰演Marvel船长，Frank Coghlan，Jr。饰演Billy Batson。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260"}})],1),_c('br'),_c('h2',[_vm._v("创作起源")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        Fawcett于1953年停止出版与Marvel相关的漫画，部分原因是DC Comics的版权侵权诉讼声称Marvel上尉是超人的副本。1972年，Fawcett将角色权利出售给DC，后者在1991年获得了整个角色家族的所有权利。此后，DC将Marvel船长和漫威家族纳入其DC宇宙，并试图多次重振该地产，取得了不同程度的成功。由于漫威漫画公司拥有的名为“Captain Marvel”的其他角色的商标冲突，DC使用商标Shazam对该角色进行了品牌推广和营销！自1972年重新引入以来。这导致许多人认为“Shazam！” 是角色的名字，DC正式更名为“Shazam！”。当重新发起于2011年的漫画书性质，他被称为“Shazam的家庭”的同事")])]),_c('v-card',{staticClass:"card-img"},[_c('v-img',{attrs:{"width":"180","height":"240","src":""}}),_c('v-card-title',{attrs:{"primary-title":""}},[_c('div',[_c('span')])])],1)],1),_c('br'),_c('h2',[_vm._v("影视作品及游戏")]),_c('hr',{staticClass:"mt-3 v-divider them